

























































































import clone from "@/lib/clone";
import dayjs from "dayjs";
import Vue from "vue";
import { Component, Prop, Watch } from "vue-property-decorator";
const { DatePicker } = require("view-design");
import _, { keys } from "lodash";

@Component({
  components: { DatePicker },
})
export default class KeepingAccount extends Vue {
  // value =  '';
  type = "-";
  // month = new Date().toISOString().split("T")[0];
  month = dayjs(new Date()).format("YYYY-MM-DD");
  beforeCreate() {
    this.$store.commit("fetchRecords");
  }
  selectMonth() {
    // console.log("month");
  }
  created() {}
  get recordList() {
    return (this.$store.state as RootState).recordList;
  }
  weekDay(date: string) {
    const week = [
      "星期天",
      "星期一",
      "星期二",
      "星期三",
      "星期四",
      "星期五",
      "星期六",
    ];

    return week[dayjs(date).day()];
  }
  get paymentRecordList() {
    const { recordList } = this;
    if (recordList.length === 0) {
      return [];
    }
    const newList = clone(recordList)
      .filter((r) => r.type === "-")
      .sort(
        (a, b) =>
          dayjs(b.createdAt as string).valueOf() -
          dayjs(a.createdAt as string).valueOf()
      );
    if (newList.length === 0) {
      return [];
    }
    type Result = { title: string; total?: number; items: RecordItem[] }[];
    const result: Result = [
      {
        title: dayjs(newList[0].createdAt as string).format("YYYY-MM-DD"),
        items: [newList[0]],
      },
    ];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt as string), "day")) {
        last.items.push(current);
      } else {
        result.push({
          title: dayjs(current.createdAt as string).format("YYYY-MM-DD"),
          items: [current],
        });
      }
    }
    result.map((group) => {
      group.total = group.items.reduce((sum, item) => {
        //   console.log(sum);
        //   console.log(item);
        return sum + item.amount;
      }, 0);
    });
    //    console.log('result');
    //  console.log(result);

    return result;
  }
  get incomeRecordList() {
    const { recordList } = this;
    if (recordList.length === 0) {
      return [];
    }
    const newList = clone(recordList)
      .filter((r) => r.type === "+")
      .sort(
        (a, b) =>
          dayjs(b.createdAt as string).valueOf() -
          dayjs(a.createdAt as string).valueOf()
      );

    if (newList.length === 0) {
      return [];
    }
    type Result = { title: string; total?: number; items: RecordItem[] }[];
    const result: Result = [
      {
        title: dayjs(newList[0].createdAt as string).format("YYYY-MM-DD"),
        items: [newList[0]],
      },
    ];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt as string), "day")) {
        last.items.push(current);
      } else {
        result.push({
          title: dayjs(current.createdAt as string).format("YYYY-MM-DD"),
          items: [current],
        });
      }
    }
    result.map((group) => {
      group.total = group.items.reduce((sum, item) => {
        //   console.log(sum);
        //   console.log(item);
        return sum + item.amount;
      }, 0);
    });
    //    console.log('result');
    //  console.log(result);

    return result;
  }
  get totalList() {
    const today = new Date();
    console.log(dayjs(today).isSame(this.month, "month"));

    const { recordList } = this;
    if (recordList.length === 0) {
      return [];
    }
    const newList = clone(recordList)
      .filter((r) => dayjs(r.createdAt).isSame(this.month, "month"))
      .sort(
        (a, b) =>
          dayjs(b.createdAt as string).valueOf() -
          dayjs(a.createdAt as string).valueOf()
      );

    // console.log(newList);
    if (newList.length === 0) {
      return [];
    }
    type Result = {
      title: string;
      paymentRecord?: number;
      incomeRecord?: number;
      items: RecordItem[];
    }[];
    const result: Result = [
      {
        title: dayjs(newList[0].createdAt as string).format("YYYY-MM-DD"),
        items: [newList[0]],
      },
    ];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt as string), "day")) {
        last.items.push(current);
      } else {
        result.push({
          title: dayjs(current.createdAt as string).format("YYYY-MM-DD"),
          items: [current],
        });
      }
    }
    result.map((group) => {
      group.paymentRecord = group.items.reduce((sum, item) => {
        return item.type === "-" ? sum + item.amount : sum + 0;
      }, 0);
    });
    result.map((group) => {
      group.incomeRecord = group.items.reduce((sum, item) => {
        return item.type === "+" ? sum + item.amount : sum + 0;
      }, 0);
    });
    console.log(result[0].paymentRecord);
    console.log(result[0].incomeRecord);
    console.log(result);
    return result;
  }
  get amountTotal() {
    const paymentRecord = this.totalList.reduce((sum, a) => {
      return sum + (a.paymentRecord || 0);
    }, 0);
    console.log(paymentRecord);

    const incomeRecord = this.totalList.reduce((sum, a) => {
      return sum + (a.incomeRecord || 0);
    }, 0);
    console.log(incomeRecord);
    const amountTotal = { paymentRecord, incomeRecord };
    return amountTotal;
  }

  open: boolean = false;
  handleClick() {
    this.open = !this.open;
    if(this.open ===false) this.open = true;
  }
  handleChange(date: string) {
    this.month = date + "-01";
    console.log(this.month);
    this.open = !this.open;
  }
  handleAuthors () {
      this.open = false;
  }
}
