





















































































import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import NotesTest from "@/components/Money/NotesTest.vue";
import TagsTest from "@/components/Money/TagsTest.vue";
import TabsTest from "@/components/Money/TabsTest.vue";
import recordTypeList from "@/constants/recordTypeList";
const { DatePicker } = require("view-design");
import day from "dayjs";

import { plugin, Toast}  from 'lemon-ui-demo';
import 'lemon-ui-demo/dist/index.css' 

Vue.use(plugin)
// console.log('DatePicker');
console.log(plugin);

@Component({
  components: { NumberPadTest, NotesTest, DatePicker, TabsTest, TagsTest},
})
export default class NumberPadTest extends Vue {
  record: RecordItem = {
    tags: ({} as Tag),
    notes: "",
    type: "-",
    amount: 0,
    createdAt: day(new Date().toISOString()).format('YYYY-MM-DD'),
  };
  recordTypeList = recordTypeList;
  day = day;
  h = 0;
  created() {
    this.$store.commit("fetchRecords");
    console.log(this.h); 
    // this.$store.commit('fetchTags');
  }
  mounted() {
    console.log(this.h);
    this.h = document.body.clientHeight;
    console.log(this.h);
  }

  // } this.number.toString() ||''
  output: string = "0";
  // i: number =  0;
  inputContent(event: MouseEvent) {
    const div = event.target as HTMLDivElement;
    const input = div.textContent as string;

    if (this.output.length === 10) {
      return;
    }
    if (this.output === "0") {
      if ("0123456789".indexOf(input) >= 0) {
        this.output = input;
      } else {
        this.output += input;
      }
      return;
    }
    if (this.output.indexOf(".") >= 0 && input === ".") {
      //console.log(this.output.split('.'));

      return;
    }
    if (this.output.indexOf(".") >= 0) {
      let arr = this.output.split(".");
      if (arr[1].length >= 2) {
        return;
      }
    }
    this.output += input;
  }
  remove() {
    if (this.output.length <= 1) {
      this.output = "0";
      return;
    }
    this.output = this.output.slice(0, -1);
  }
  clear() {
    this.output = "0";
  }
  equalNumber = 0;
  symbol = true;
  equalSymbol = true;//TODO 可优化
  increase() {
    this.equalNumber = parseFloat(this.output);
    this.output = "0";
    this.symbol = true;
    this.equalSymbol = false;
  }

  decrease() {
    this.equalNumber = parseFloat(this.output);
    this.output = "0";
    this.symbol = false;
    this.equalSymbol = false;
  }
  equalOutput() {
    if (this.symbol) {
      this.output = (this.equalNumber + parseFloat(this.output)).toString();

      let arr = this.output.split(".");
      if (arr[1] && arr[1].length > 2) {
        let array2 = arr[1].split("");
        this.output = arr[0] + "." + array2[0] + array2[1];
        //TODO  30.4+0.2 =30.59
      }

      this.equalSymbol = true;

      return;
    }
    const a = this.equalNumber - parseFloat(this.output);

    if (a < 0) {
      alert("不支持负数哦");
      this.output = "0";
    } else {
      this.output = a.toString();
      let arr = this.output.split(".");
      if (arr[1] && arr[1].length > 2) {
        let array2 = arr[1].split("");
        this.output = arr[0] + "." + array2[0] + array2[1];
      }
    }
    this.equalSymbol = true;
  }
  saveRecord() {
    this.record.amount = parseFloat(this.output);
    if (!this.record.tags.name) {
      return window.alert("请选择一个标签");
    }
    if (this.record.amount === 0) {
      return window.alert("请输入金额");
    }
    this.$store.commit("createRecord", this.record);
    if (this.$store.state.createRecordError === null) {
      // window.alert("创建成功");
      this.$router.replace('/keep')
      this.record.notes = "";
    }
    this.output='0';
  }
  recordNotes(){
    // return $toast('点击弹出提示',{position: 'center'})
  }

  open: boolean = false;
  // value3: string = '';
  handleClick() {
    this.open = !this.open;
  }
  handleChange(date: string) {
    this.record.createdAt = date;
    this.open = false;
    // console.log(typeof date);
  }
  handleAuthors () {
      this.open = false;
  }
}
